import React, { useState } from 'react';
import logo from './logo.svg';
import './App.css';
import { Link, Route, Switch } from 'react-router-dom';
import { FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import { faBars, faTimes } from '@fortawesome/free-solid-svg-icons'  
import { Simulate } from 'react-dom/test-utils';
import About from './About.js';
import 데이터 from './data.js';

function App() {

  let [aboutContents, aboutContentsSet] = useState(데이터);
  let [menuBar, menuBarChange] = useState(false);
  let [aboutToggle, aboutToggleSet] = useState(false);



  return (
    <div className="App">
      <div id="contents">
      <div id="header">
            <Link to="/"><h1 id="logo">dududWEB*</h1></Link>
            <div className="menu-wrap">
              <a id="openMenu"href="#" onClick={
                 ()=>{ if(menuBar == false){ 
                      menuBarChange(true)
                    }else{
                      menuBarChange(false)
                    }
                  } }>
                  <FontAwesomeIcon className="ddd" icon={ menuBar == false ? faBars : faTimes } /></a>
                  <SideMenu menuBar={menuBar}></SideMenu>  
            </div>
            <nav className="navbar">
              <ul>
                <li><Link to="/about">ABOUT</Link></li>
                <li><Link to="/project">PROJECT</Link></li>
              </ul> 
            </nav>
       </div>
      <Route exact path="/">
        <Section />
      </Route>
      <Route exact path="/about" basement="/about">
        <About aboutContentsSet = {aboutContentsSet} aboutContents = {aboutContents} aboutToggle={aboutToggle} aboutToggleSet={aboutToggleSet}/>
      </Route>
      <Footer/>
      </div>
    </div>
  );
}

function SideMenu(props){ // 반복적으로 출현하는, 자주 변경되는 html를 컴포넌트화 하는게 유리하다.
      if(props.menuBar == true){
        return   <div className={props.menuBar == true ? "showMenu" : "sideMenu" } >
                 <div className="list-wrap">
                  <ul className="list-menu">
                    <li><Link to="/about">ABOUT</Link></li>
                    <li><Link to="/project">PROJECT</Link></li>
                  </ul> 
                 </div>
               </div>  
  } 
  else{
    return null
  }
}
function Section(){
  return(
         <section id="section">
            <div className="main-box-wrap">
              <div className="main-box">
                <Link to="/about">  
                  <img src="img/22.jpeg" alt="" />
                </Link>  
                  <h2 className="main-text">ABOUT</h2>
              </div>
              <div class="main-box">
                <Link to="/project">  
                  <img src="img/eye.jpg" alt="" />
                </Link> 
                  <h2 className="main-text">PROJECT</h2>
              </div>
            </div>  
         </section>
  )
}
function Footer(){
    return(

        <footer id="footer">
          <div className="footer-wrap">
            <p>dududweb*</p>
            <p><a href="mailto:joodudsdx@gmail.com" title="마우스 올렸을 때 나타나는 문구">joodudsdx@gmail.com</a></p>
            <p>instagram <a href="https://www.instagram.com/joodudud/">@joodudud</a></p>
            <p>01029852796</p>
          </div>
          <div className="footer-wrap">
            <p><a href="https://github.com/dududweb">github.com/dududweb</a></p>
            <p><a href="https://dududweb.tistory.com/">dududweb.tistory.com/</a></p>
            <br/>
            <p>**</p>
          </div>
        </footer>
    )
}
export default App;
