import React, { useState } from 'react';
import './data.js';


function About(props){
  return(
       <section id="about-section">    
                  {
                    props.aboutContents.map(function(a,i){
                      return(
                        <div className="panel-question">
                        <div className="panel-heading">
                          <div className="panel-box" onClick={ ()=>{props.aboutToggleSet(!props.aboutToggle)} }>
                            <span className="panel-text">{ props.aboutContents[i].title }</span>
                            <span className="panel-toggle"> { props.aboutToggle == false ? "+" : "-" }</span>
                          </div>
                        </div>
                            {
                              props.aboutToggle == true
                              ? <AnswerTab  aboutContents={props.aboutContents[i]} i={i} />
                              : null
                            }
                         </div>    
                      )
                    })
                  } 
        </section>
  )
}

function AnswerTab(props){
    return(
           <div className="panel-body">
            <div className="cate-img">
              <img src={"img/img" + (props.i+1) + ".jpeg"} alt=""/>
            </div>
              <p>{ props.aboutContents.content }</p>
           </div>

    )
}

export default About;